module.exports = "<div class=one><div v-if=\"type=='recommend'||type=='normal'\" class=works-one-owner><div v-link=\"{name:'person-other',params: { id: works.owner.user_id}}\" class=works-one-user><img class=works-one-avatar v-bind:src=works.owner.avatar_url> <img v-if=\"works.owner.is_vip==1\" class=works-one-avatar-vip src=http://7xqamv.com2.z0.glb.qiniucdn.com/icon-is-vip.png> <span class=works-one-nickname>{{works.owner.nickname}}</span></div><span v-if=\"type=='normal'\" class=works-one-follow v-on:click=dealFollow v-bind:class=\"{'works-one-follow-deal-border': status=='unfollow'}\">{{statusContent}}</span> <span v-if=\"type=='recommend'&&works.common_flags.mark_text!=null\" class=works-one-mark v-bind:style=\"{ color: '#'+works.common_flags.mark_color, borderColor: '#'+works.common_flags.mark_color }\">{{works.common_flags.mark_text}}</span></div><div v-link=\"{name:'works-detail',params: { id: works.pub_id}}\"><div v-if=\"type=='recommend'||type=='nouser'\" class=works-one-recommend-title>{{works.title}}</div><div class=works-one-thumbnail><div v-if=\"works.common_flags.topleft_text!=null\" class=works-one-thumbnail-topleft-text><p class=works-one-thumbnail-topleft-text-0>{{topleftText[0]}}</p><p>{{topleftText[1]}}</p></div><div v-bind:class=\"{'works-one-thumbnail-topleft-text-top': works.common_flags.topleft_text!=null}\"><div v-if=\"works.thumbnail.type=='image'\" class=works-one-thumbnail-image><span v-if=\"works.image_count>1\" class=works-one-image-count>{{works.image_count}}图</span> <img class=works-one-thumbnail-image-img v-bind:class=\"{'works-one-thumbnail-image-img-top': works.image_count>1}\" v-bind:src=works.thumbnail.content.thumbnail_url></div><div v-if=\"works.thumbnail.type=='text'\" class=works-one-thumbnail-text><p class=works-one-thumbnail-text-content>{{works.thumbnail.content.text}}</p></div><div v-if=\"works.thumbnail.type=='ihp_v'||works.thumbnail.type=='youku_v'\" class=works-one-thumbnail-video><img class=works-one-thumbnail-video-img v-bind:src=works.thumbnail.content.thumbnail_url> <img class=works-one-thumbnail-video-play src=http://7xqamv.com2.z0.glb.qiniucdn.com/icon-video-play-c.png></div></div></div><div v-if=\"type=='normal'\" class=works-one-normal-title>{{works.title}}</div></div><div v-if=\"works.tags!=null\" class=works-one-works-tags><ul><li v-for=\"tag in works.tags\"><a v-bind:href=\"'#!/tag-detail/'+tag.tag_id\">{{tag.tag_name}}</a></li></ul></div><p v-if=\"type=='nouser'\" class=works-one-pubdate>{{works.pub_date}}</p><div class=works-one-like-comment><span class=works-one-content-like v-on:click=dealLike><img class=works-one-icon v-bind:src=likedImgSrc> <span class=works-one-count>{{works.likes_count}}</span></span> <span class=works-one-content-comment v-on:click=dealComment><img class=works-one-icon src=http://7xqamv.com2.z0.glb.qiniucdn.com/icon-comments.png> <span class=works-one-count>{{works.comments_count==0 ? '评论' : works.comments_count}}</span></span></div><div class=works-one-blank></div></div>";


